.topnav {
  overflow: visible;
  background-color: #009688;
  height: 8vh;
}

.topnav a {
  float: right;
  color: #f2f2f2;
  text-align: center;
  padding: 2vh 3vh;
  text-decoration: none;
  font-size: 3vh;
  display: block;
}

.topnav a:hover {
  background-color: #ddd;
  color: black;
}

.NavBar-ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  height: 8vh;
  background-color: #009688;
}

.left {
  float: left;
  height: 100%;
  display: flex;
}

.right {
  float: right;
  height: 100%;
}

.Navbar-li a {
  display: block;
  color: white;
  text-align: center;
  padding: 2vh 3vh;
  height: 100%;
  text-decoration: none;
  font-family: 'Raleway', sans-serif;
  font-size: 3vh;
}

.Navbar-li a:hover {
  background-color: #111;
}

.Navbar-icon {
  height: calc(18px  + 1.1vw);
  width: calc(18px  + 1.1vw);
}

.Navbar-resume {
  background-color: white; /* Green */
  border: none;
  color: black;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: calc(20px);
  margin-left: 10px;
  cursor: pointer;
  border-radius: 5px;
  font-family: 'Raleway', sans-serif;
  font-weight: 500;
}